import { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import React from 'react';
import './Carousel.css';

const slides = [
  {
    id: 1,
    imagePath: "assets/carousel-images/5.jpeg",
    title: "  ",
    text: "    ,     .     ,     3-6  ,   .      ,            ."
  },
  {
    id: 2,
    imagePath: "assets/carousel-images/3.jpeg",
    title: "  ",
    text: "  ,             ,   ."
  },
  {
    id: 3,
    imagePath: "assets/carousel-images/4.jpeg",
    title: "   ",
    text: "    .    -,      ,  ,  ,  .        ,      ."
  },
  {
    id: 4,
    imagePath: "assets/carousel-images/7.jpeg",
    title: "    ",
    text: "        ,       .  ,      .          ."
  },
  {
    id: 5,
    imagePath: "assets/carousel-images/6.jpeg",
    title: " ",
    text: "     ,    ,          .    , . ,     ."
  }
];

function ControlledCarousel() {
  const [index, setIndex] = useState<number>(0);

  const handleSelect = (selectedIndex: number) => {
    setIndex(selectedIndex);
  };

  return (
    <Carousel activeIndex={index} onSelect={handleSelect} className='w-80' style={{height:'400px', borderRadius: '0.75rem'}}>
      {slides.map((slide) => (
        <Carousel.Item key={slide.id}>
          <div className='w-100 h-100 d-flex align-items-stretch justify-content-center'>
            <div className="carousel-overlay"></div>
            <img className="d-block" src={slide.imagePath} alt={`Slide ${slide.id}`} style={{height:'400px'}}/>
            <Carousel.Caption className='slide-text'>
              <h3>{slide.title}</h3>
              <p>{slide.text}</p>
            </Carousel.Caption>
          </div>
        </Carousel.Item>
      ))}
    </Carousel>
  );
}

export default ControlledCarousel;
